@use '../utils/mixins' as *;

.burger {
  &::before {
    content: '';
    background-image: url(../images/Icon-Burger-menu.svg);
    background-size: cover;
    display: inline-block;
    width: 24px;
    height: 18px;

    @include for-tablet {
      width: 32px;
      height: 32px;
    }

    @include for-desktop {
      width: 32px;
      height: 32px;
    }
  }

  &::after {
    display: none;
    content: '';
    background-image: url(../images/Icon-Burger-menu-hover.svg);
    background-size: cover;
    width: 24px;
    height: 18px;

    @include for-tablet {
      width: 32px;
      height: 32px;
    }

    @include for-desktop {
      width: 32px;
      height: 32px;
    }
  }


  &:hover {
    &::before {
      display: none;
    }

    &::after {
      display: inline-block;
    }
  }

  &__label {
    @include visually-hidden;
  }
}